/*# sourceMappingURL=bootstrap-grid.min.css.map */
/* Author: 01010011 01101000 01100001 01101101 01100101 01100101 01110010 */

/* imports  */

/* FONT-INCLUDES */

@font-face {
  font-family: Nexa;
  font-weight: 300;
  src: url(../../static/media/NexaLight.2bac835e.otf);
}

@font-face {
  font-family: Nexa;
  font-weight: 400;
  src: url(../../static/media/NexaRegular.ac2b3648.otf);
}

@font-face {
  font-family: Nexa;
  font-weight: 500;
  src: url(../../static/media/NexaBold.c2b99812.otf);
}

/* css reset  */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* BASE-STYLES */

body {
  /* font-family: Nexa, sans-serif; */
  font-family: "Nunito", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #2f2b2b;
}

button {
  cursor: pointer;
}

.text-center {
  text-align: center !important;
}

.text-left {
  text-align: left !important;
}

.mb-20 {
  margin-bottom: 20px !important;
}

.app-body {
  border-top: 4px solid #c9b16d;
}

.pt-0 {
  padding-top: 0 !important;
}
.w-100 {
  width: 100% !important;
}

.overlay {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100%;
  transition: 0.3s all ease-in;
  z-index: 1;
}
.page-container {
  border-radius: 10px;
}

.dflex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.dInline {
  display: inline-block;
}

/* button icon */

.icon-btn {
  padding: 14px 10px;
  border: 1px solid #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s all ease-in-out;
  border-radius: 2px;
}

.icon-btn.gold {
  border: 1px solid #c9b16d;
  transition: 0.3s all ease-in-out;
}

.icon-btn.gold:hover .btn-icn {
  fill: white;
  transition: 0.3s all ease-in-out;
}

.icon-btn:hover {
  background: #c9b16d;
  transition: 0.3s all ease-in-out;
}

/* info-container  */

.info-container {
  opacity: 0.5;
  border: 2px dashed #c9b16d;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 12px;
}

.info-icon {
  margin-top: 2px;
  margin-right: 10px;
}

.info-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  display: flex;
  align-items: center;
  color: #002654;
  margin-bottom: 0;
}

.info-text a {
  color: #002654;
  font-weight: 700;
  margin-left: 5px;
}

/* header  */

.header-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  width: 100%;
}

.logo {
  flex-grow: 1;
  margin-left: 20px;
}

.header-content.white {
  background: rgba(255, 255, 255, 0.8);
  transition: 0.3s all ease-in-out;
  padding: 15px 40px;
}

.header-icon {
  border: 1.5px solid #ffffff;
  border-radius: 2px;
  padding: 11px 15px;
  display: flex;
  justify-content: center;
}

.header-content.white .header-icon {
  border: 1.5px solid #c9b16d;
  transition: 0.3s all ease-in-out;
}

.header-icon:hover {
  background: #c9b16d;
  transition: 0.5s all ease-in-out;
}

.ham-menu-icon {
  margin: 0;
  top: 20px;
  z-index: 9;
}

/* ham-menu  */

#hamburger {
  cursor: pointer;
  padding: 20px 38px 23px 16px;
  border: 1.5px solid #ffffff;
  border-radius: 2px;
}

.header-content.white #hamburger {
  border: 1.5px solid #c9b16d;
  transition: 0.3s all ease-in-out;
}

#hamburger:hover {
  background: #c9b16d;
  transition: 0.5s all ease-in-out;
}

#hamburger span,
#hamburger span:before,
#hamburger span:after {
  cursor: pointer;
  border-radius: 1px;
  height: 2px;
  width: 22px;
  background: #fff;
  position: absolute;
  display: inline-block;
  content: "";
}

.header-content.white #hamburger span,
.header-content.white #hamburger span:before,
.header-content.white #hamburger span:after {
  background: #c9b16d;
  transition: 0.3s all ease-in-out;
}

.header-content.white #hamburger:hover span,
.header-content.white #hamburger:hover span:before,
.header-content.white #hamburger:hover span:after {
  background: #fff;
  transition: 0.3s all ease-in-out;
}

#hamburger span:before {
  top: -7px;
}
#hamburger span:after {
  bottom: -7px;
}
/*
#hamburger span, #hamburger span:before, #hamburger span:after {
  transition: all 200ms ease-in-out;
}
*/
#hamburger span,
#hamburger span:before,
#hamburger span:after {
  transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1); /* easeInOutCubic */
}
#hamburger.active span {
  background-color: transparent;
}
#hamburger.active span:before,
#hamburger.active span:after {
  top: 0;
}
#hamburger.active span:before {
  transform: rotate(45deg);
}
#hamburger.active span:after {
  transform: translateY(-10px) rotate(-45deg);
  top: 10px;
}

/* side-nav */

.side-menu-container {
  position: relative;
  z-index: 2;
}

.side-menu-panel {
  width: 380px;
  background: #c9b16d;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  padding: 100px 20px 20px 20px;
}

.menu-list {
  list-style: none;
  padding-left: 0px;
  margin-left: -10px;
}

.menu-item {
  text-transform: uppercase;
  margin-bottom: 30px;
}

.menu-item a {
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  color: #ffffff;
  text-decoration: none;
}

/* home  */

.banner-img {
  position: relative;
}

.banner-img img {
  width: 100%;
}

.banner-content {
  position: absolute;
  bottom: 80px;
  display: flex;
  justify-content: center;
  width: 100%;
}

/* slick slider  */

.slick-next {
  right: 4rem;
}

.slick-prev {
  left: 4rem;
  z-index: 1;
}

.slick-dots {
  bottom: 2rem;
  list-style: none;
}
.slick-dots li {
  list-style: none;
}

.slick-dots:hover {
  list-style: none;
}

.slick-dots li button {
  background: transparent;
  border: 0.1rem solid #ffffff;
  border-radius: 50%;
  height: 1rem;
  width: 1rem;
  text-decoration: none;
}
.slick-dots li button:hover {
  text-decoration: none;
}

.slick-dots li.slick-active button {
  background: #c9b16d;
  border: 0.1rem solid #c9b16d;
}

.slick-dots li.slick-active button:before {
  opacity: 0;
}

.slick-dots li button:before {
  opacity: 0;
