/* modal  */

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 11;
}

.modal-box {
  background: #ffffff;
  border: 1px solid #c9b16d;
  box-sizing: border-box;
  padding: 60px;
  width: 560px;
  margin: auto;
  max-height: 90vh;
  overflow: hidden;
}
.modal-overlay {
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100%;
  transition: 0.3s all ease-in;
  z-index: 10;
  animation-name: modal;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

@keyframes modal {
  0% {
    background: rgba(0, 0, 0, 0);
  }
  100% {
    background: rgba(0, 0, 0, 0.5);
  }
}

.modal-team-info {
  margin-top: 40px;
